















import Vue from "vue"

export default Vue.extend({
  name: "FilterContainer",
  props: ['allRoutes'],
  data () {
    return {
      checkedValues: []
    }
  },
  methods: {
    idString (id) {
      return `route-${id}`
    },
    resetChecked () {
      this.checkedValues = []
      Array.from(document.querySelectorAll('input[type=checkbox]')).forEach(element => {
        element.checked = false
      });
    }
  },
  watch: {
    checkedValues (newVal, oldVal) {
      this.$emit('change', newVal)
    }
  }
})



